.Home {
  text-align: center;
  font-family: sans-serif;
  font-size: 1.5rem;
  /* font-weight: bold; */
  padding-top: 50px;
}

.Home .title {
  text-align: center;
  font-family: sans-serif;
  font-size: 1.5rem;
  font-weight: bold;
}

.Note {
  /* text-align: center; */
  background-color: #8BA0C6;
  padding: 0.5rem 0rem;
  border-radius: 20px;
  margin-left: 8px;
  margin-right: 8px;
  margin-top: 8px;
  padding-left: 8px;
  padding-right: 8px;
}

.Note li {
  font-size: 14px;
  margin-left: 10px;
  list-style-type: circle;
  display: list-item;
}

.Note .title {
  font-family: sans-serif;
  font-size: 1rem;
  font-weight: bold;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}