.navigation-bar {
    background-color: #8BA0C6;
    height: 60px;
    /* width: 100%; */
    display: flex;
    align-items: center;
    position: relative;
    padding: 0.5rem 0rem;
    border-radius: 20px;
    margin-left: 8px;
    margin-right: 8px;
    margin-top: 8px;
  }
  
  .navigation-landing {
    text-decoration: none;
    color: black;
    /* font-size: 1.3rem; */
    margin-left: 1rem;
    font-family: sans-serif;
    font-weight: bold;
    font-size: 2rem;
  }

  .navigation-menu {
    margin-left: auto;
  }
  .navigation-menu ul {
    display: flex;
    padding: 0;
    z-index: 10;
  }
  .navigation-menu li {
    list-style-type: none;
    margin: 0 1rem;
  }
  .navigation-menu li a {
    text-decoration: none;
    display: block;
    width: 100%;
    color: black;
    font-family: sans-serif;
    font-weight: bold;
    font-size: 1rem;
  }
  
  .hamburger {
    border: 0;
    height: 40px;
    width: 40px;
    padding: 0.5rem;
    border-radius: 50%;
    background-color: #283b8b;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
    position: absolute;
    top: 50%;
    right: 25px;
    transform: translateY(-50%);
    display: none;
  }
  .hamburger:hover {
    background-color: #2642af;
  }


  @media screen and (max-width: 768px) {
    .hamburger {
      display: block;
    }
  }

  @media screen and (max-width: 768px) {
    .navigation-menu ul {
      display: none;
    }
  }
  
  @media screen and (max-width: 768px) {
    .navigation-menu ul {
      position: absolute;
      top: 60px;
      left: 0;
      flex-direction: column;
      width: 100%;
      height: calc(100vh - 77px);
      background-color: white;
      border-top: 1px solid black;
    }
    .navigation-menu li {
      text-align: center;
      margin: 0;
    }
    .navigation-menu li a {
      color: black;
      width: 100%;
      padding: 1.5rem 0;
    }
    .navigation-menu li:hover {
      background-color: #eee;
    }
  }
  
  @media screen and (max-width: 768px) {
    .navigation-menu ul {
      display: none;
    }
  }
  
  @media screen and (max-width: 768px) {
    .navigation-menu.expanded ul {
      display: block;
    }
  }